import { FuseGetFunction } from "fuse.js";

export interface IFuseResult {
  item: any;
  refIndex: number;
  score: number;
}

export interface IOptions {
  isCaseSensitive?: boolean; // Indica si las comparaciones deben ser sensibles a maysculas y minsculas. (def: false)
  includeScore?: boolean; // Si la puntuacin debe incluirse en el conjunto de resultados. Una puntuacin de 0 indica una coincidencia perfecta, mientras que una puntuacin de 1 indica una falta de coincidencia completa. (def: false)
  includeMatches?: boolean; // Si las coincidencias deben incluirse en el conjunto de resultados. Cuando es verdadero, cada registro en el conjunto de resultados incluir los ndices de los caracteres coincidentes. En consecuencia, se pueden usar para resaltar.(def: false)
  shouldSort?: boolean; // Si se debe ordenar la lista de resultados por puntuacin. (def: true)
  findAllMatches?: boolean; // Cuando es verdadero, la funcin de coincidencia continuar hasta el final de un patrn de bsqueda, incluso si ya se ha encontrado una coincidencia perfecta en la cadena. (def: false)
  minMatchCharLength?: number; // Solo se devolvern las coincidencias cuya longitud exceda este valor. (Por ejemplo, si desea ignorar las coincidencias de un solo carcter en el resultado, configrelo en 2). (def: 1)
  location?: number; // Determina aproximadamente en qu parte del texto se encuentra el patrn que se espera encontrar. (def: 0)
  threshold?: number; // En qu punto se rinde el algoritmo de coincidencia? Un umbral de 0.0 requiere una coincidencia perfecta (tanto de letras como de ubicacin), un umbral de 1.0 coincidira con cualquier cosa. (def: 0.6)
  distance?: number; // Determina qu tan cerca debe estar la coincidencia de la ubicacin difusa (especificada por la ubicacin). Una coincidencia exacta de letras, que es la distancia de los caracteres lejos de la ubicacin difusa, se considerara como una falta de coincidencia completa. Una distancia de 0 requiere que la coincidencia est en la ubicacin exacta especificada. Una distancia de 1000 requerira una coincidencia perfecta para estar dentro de los 800 caracteres de la ubicacin que se encuentra utilizando un umbral de 0,8. (def: 100)
  useExtendedSearch?: boolean; // Cuando es verdadero, permite el uso de comandos de bsqueda tipo Unix. (def: false)
  ignoreLocation?: boolean; // Cuando es verdadero, la bsqueda ignorar la ubicacin y la distancia, por lo que no importar en qu parte de la cadena aparezca el patrn. (def: false)
  ignoreFieldNorm?: boolean; // Cuando es verdadero, el clculo de la puntuacin de relevancia (utilizada para la clasificacin) ignorar la norma de longitud de campo.
  getFn?: FuseGetFunction<any>; // La funcin que se utiliza para recuperar el valor de un objeto en la ruta proporcionada. El valor predeterminado tambin buscar rutas anidadas.
  // sortFn?: Function; // La funcin a utilizar para ordenar todos los resultados. El valor predeterminado se ordenar por puntuacin de relevancia ascendente
}
/**
 * NOTA:
 * - Para comprender mejor cmo location, threshold y distance funcionan juntos, leer en https://fusejs.io/concepts/scoring-theory.html#scoring-theory
 * - El nico momento en que tiene sentido establecer ignoreFieldNorm en true es cuando no importa cuntos trminos haya, sino solo que el trmino de consulta exista.
 */

export interface IBizySearchPipeOptions extends IOptions {
  keys?: Array<string>;
}

export class BizySearchPipeOptions implements IBizySearchPipeOptions {
  isCaseSensitive?: boolean;
  distance?: number;
  findAllMatches?: boolean;
  ignoreLocation?: boolean;
  ignoreFieldNorm?: boolean;
  includeMatches?: boolean;
  includeScore?: boolean;
  location?: number;
  minMatchCharLength?: number;
  shouldSort?: boolean;
  threshold?: number;
  useExtendedSearch?: boolean;
  keys: Array<string>;

  constructor(options: IOptions, keys?: Array<string>) {
    // Si se desea cambiar algun valor por default, este es el lugar indicado
    if (!keys) {
      keys = [];
    }

    const defaultOptions = {
      // Se activa includeScore para poder buscar internamente en propiedades de tipo array
      includeScore: true,
      // Cuando es verdadero, la bsqueda ignorar la ubicacin y la distancia, por lo que no importar en qu parte de la cadena aparezca el patrn
      ignoreLocation: true,
      // Se reduce a 0.1 el threshold (default: 0.6) para aumentar precisin en resultados
      threshold: 0.1
    };

    if (options) {
      options = {...defaultOptions, ...options};
    } else {
      options = defaultOptions;
    }

    Object.assign(this, {...options, keys: keys});
  }
}
